@import 'src/styles/helpers.scss';

.row {
  display: flex;
}

.col {
  flex-shrink: 0;

  &:nth-child(1) {
    width: calc(#{$col-part-d} * 3);
  }
  &:nth-child(2) {
    width: calc(#{$col-part-d} * 13 - #{$col-gap-d});
  }
}

.title {
  text-align: left;
}

.grid {
  margin-top: rem(60px);

  display: grid;
  grid-template-columns: repeat(4, 1fr);
  row-gap: rem(38px);
  column-gap: rem(22px);
}

.item {
  position: relative;
  z-index: 0;

  height: rem(160px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.text {
  position: relative;
  z-index: 0;

  letter-spacing: -0.04em;
  @include fsl(40px, 90%);
}

.num {
  position: absolute;
  left: calc(100% + #{rem(4px)});
  bottom: calc(100% - #{rem(4px)});

  letter-spacing: -0.02em;
  color: color(gray-second);
  @include fsl(18px, 90%);
}