@import 'src/styles/helpers.scss';

.wrapper {
  margin-top: rem(40px);
}

.inner {
  margin-top: rem(80px);
}

.row {
  display: flex;
  justify-content: space-around;
}

.card {
  width: rem(910px);
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: rem(20px);
  row-gap: rem(80px);
}

.link {
  display: flex;
  align-items: center;
  justify-content: center;
}

.text {
  color: #A6A6A6;
  letter-spacing: -0.05em;
  @include fsl(68px, 90%);
}

.icon {
  margin-left: rem(16px);

  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    --size: #{rem(24px)};
    margin-top: rem(5px);

    width: var(--size);
    height: var(--size);

    path {
      fill: #A6A6A6;
    }
  }
}