@import 'src/styles/helpers.scss';

.section {
  position: relative;
  z-index: 0;
  margin-top: rem(40px);
  padding-bottom: rem(40px);

  border-bottom: 1px solid color(borders);
}

.row {
  display: flex;
  justify-content: space-between;
}

.logo {
  display: flex;
  align-items: center;

  svg {
    width: rem(220px);
    height: auto;
  }
}

.title {
  
}

.span {
  color: color(gray-alt);
}

.list {
  margin-top: rem(46px);

  display: flex;

  counter-reset: index;
}

.item {
  counter-increment: index;

  width: calc(#{$col-part-d} * 3);
  display: flex;

  letter-spacing: -0.02em;
  @include fsl(18px, 90%);
}

.text {
  &::before {
    content: "0" counter(index);
    margin-bottom: rem(12px);

    display: block;

    text-align: center;
    color: color(gray-alt);
  }
}

.icon {
  position: absolute;
  right: rem(40px);
  bottom: rem(40px);
  z-index: 1;

  display: flex;
  align-items: center;

  border-radius: rem(24px);
  box-shadow: 0px 29px 32px rgba(0, 0, 0, 0.3);
  
  svg {
    --size: #{rem(100px)};

    width: var(--size);
    height: var(--size);
  }
}